* {
  margin: 0;
  padding: 0;
}

body {
  width: 100vw;
  min-height: 100vh;
  background: #dc143c;
  font-family: sans-serif;
}

button:hover {
  cursor: pointer;
  background-color: #73e831;
}

ul {
  list-style-type: none;
}

.container {
  min-width: 700px;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  display: flex;
}

h1 {
  color: #fff;
  font-size: 3rem;
}

.todo-form {
  margin: 40px 0;
}

.todo-input {
  width: 250px;
  border: none;
  border-radius: 5px;
  outline: none;
  margin-right: 10px;
  padding: 10px;
  font-size: 1rem;
}

.add-button {
  color: #fff;
  background-color: #00f;
  border: none;
  border-radius: 5px;
  outline: none;
  padding: 7px;
  font-size: 1.2rem;
}

.todo-items {
  min-width: 350px;
}

.item {
  background-color: #fff;
  margin-top: 5px;
  padding: 10px;
  font-size: 1.1rem;
}

.item:first-child {
  border-top-left-radius: 7px;
  border-top-right-radius: 7px;
}

.item:last-child {
  border-bottom-left-radius: 7px;
  border-bottom-right-radius: 7px;
}

.checkbox {
  margin-right: 10px;
}

.delete-button {
  float: right;
  background-color: #dc143c;
  border: none;
  border-radius: 7px;
  outline: none;
  margin-left: 10px;
  padding: 2px 5px;
  font-size: 1.1rem;
  font-weight: 550;
}

.checked {
  text-decoration: line-through;
}

